<template>
    <b-card no-body>
        <b-card-header header-tag="header" class="p-1" >
            <b-btn block @click="showCollapse=!showCollapse">{{description}}</b-btn>
        </b-card-header>
        <b-collapse :id="value.id" v-model="showCollapse" >
            <p>Path: {{value.id}}</p>
            <p>Descripción: {{value.description||"Sin Descripcion"}}</p>
            <ITDForm v-model="value" :form="form" :config="{submitText:'',resetText:''}"/>
        </b-collapse>
    </b-card>
</template>

<script>
export default {
    props:["value"],
    data(){
        return {
            showCollapse:false,
            hidden:false,
            form:[
                {
                    name:"methods",
                    label:"Permisos",
                    type:"checkbox-group",
                    options:[
                        "GET",
                        "POST",
                        "PUT",
                        "DELETE"
                    ],
                    value:["GET"]
                },
            ],
        };
    },
    computed:{
        description(){
            return this.value.description||this.value.id;
        }
    },
    watch:{
        value:{
            handler(val){
                console.log("VALUE");
                console.log(val);
            },
            deep:true
        }
    },
    mounted(){
        console.log("FUNC");
        console.log(this.form);
        console.log(this.value);
    }
}
</script>



