<template>
<b-container>
    <b-row>
        <b-col>
            <h3>Funciones Disponibles</h3>
            <b-list-group>
                <api-request :resource="getModel" v-model="model">
                    <draggable v-if="model" class="draggable" v-model="model.API" :options="{group:'functions'}">
                            <b-list-group-item v-for="func in model.API" :key="func.path">{{func.description||func.path}}</b-list-group-item>
                    </draggable>
                </api-request>
            </b-list-group>
        </b-col>
        <b-col>
            <h3>Funciones Asignadas</h3>
            <b-form-checkbox buttons button-variant="primary" v-model="showAll">Toggle All</b-form-checkbox>
            <b-btn button-variant="primary" @click="invert">Invert All</b-btn>
            <draggable class="draggable rounded border border-primary" v-model="funcs"  :options="{group:'functions'}">
                <div v-for="(func,id) in funcs">
                    <func v-model="funcs[id]" :key="func.path" ref="func"/>
                </div>
            </draggable>
        </b-col>
    </b-row>
</b-container>
</template>

<script>
import Func from "./Func";
import draggable from "vuedraggable";
export default {
    props:["value"],
    components:{
        Func,
        draggable
    },
    data(){
        return {
            showAll:false,
            model:null,
            funcs:Array.from(this.value||[])
        }
    },
    watch:{
        showAll(val){
            this.$refs.func.forEach(el=>{
                el.showCollapse=this.showAll;
            })
        },
        funcs:{
            handler(val){
                this.$emit("input",val)
            },
            deep:true
        }
    },
    methods:{
        invert(){
            this.$refs.func.forEach(el=>{
                el.showCollapse=!el.showCollapse;
            })
        },
        getModel(){
            return this.$http.get("/APP/AAA/Func.json").then(({body})=>body);
        },
        saveFunctions(){
            console.log(this.funcs2);
        }
    }
}
</script>

<style scoped>
.draggable {
    min-height:50px;
}
</style>



